////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////
.subNavSec{
	margin-top: 100/$design-num-sp*100vw;
	ul{
		@include killSpace;
	}
	li{
		margin-right: 26/$design-num-sp*100vw;
		&:last-child{
			margin-right: 0;
		}
		img{
			@include imgset;
		}
		svg{
			display: block;
			transition: .3s;
		}
		&.account{
			position: relative;
			top: -2/$design-num-sp*100vw;
			img{
				width: 39/$design-num-sp*100vw;
			}
			svg{
				width: 39/$design-num-sp*100vw;
				height: 39/$design-num-sp*100vw;
			}
		}
		&.instagram{
			img{
				width: 40/$design-num-sp*100vw;
			}
			svg{
				width: 40/$design-num-sp*100vw;
				height: 40/$design-num-sp*100vw;
			}
		}
		&.twitter{
			img{
				width: 44/$design-num-sp*100vw;
			}
			svg{
				width: 44/$design-num-sp*100vw;
				height: 36/$design-num-sp*100vw;
			}
		}
	}
	a{
		svg{
			fill:$key-gray;
		}
	}
}
@media screen and (min-width: $break-point-middle) {
.subNavSec{
	//margin-top: 100px;
	position: absolute;
	bottom: 44px;
	li{
		margin-right: 18px;
		&:last-child{
			margin-right: 0;
		}
		&.account{
			top: 0;
			img{
				width: 18px;
			}
			svg{
				width: 18px;
				height: 18px;
			}
		}
		&.instagram{
			img{
				width: 18px;
			}
			svg{
				width: 18px;
				height: 18px;
			}
		}
		&.twitter{
			img{
				width: 18px;
			}
			svg{
				width: 18px;
				height: 18px;
			}
		}
	}
	a:hover{
		svg{
			fill:$key-black;
		}
	}
}
}
