@charset "utf-8";
/* ---------------------------
    util
--------------------------- */

/* logtag */
#logtag { display: none; }

/* clearfix */
.clearfix{zoom:1;}
.clearfix:after{ content : ''; display : block; clear : both; }

/**
   * force float-left
   */
.fl{float:left !important;}

/**
   * force float-right
   */
.fr {float: right !important;}

/**
   * force clear-both
   */
.clear {clear:both !important;}


/* position */
.relative {
  position: relative;
}
.absolute {
  position: absolute;
}

/* text-align */

/**
* force align-left
*/
.al { text-align: left !important; }

/**
* force align-right
*/
.ar { text-align: right !important; }

/**
* force align-center
*/
.ac { text-align: center !important; }

/* bold */
.bold, strong {
  font-weight: bold;
}

/* caption */
.caption {
  font-size: 11px;
}

